@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900);
// Some vars
$animation-duration: 5.4s;

$size: 15px;
$margin: 5px;
$color: #083c5d;
$next: $size + $margin;

$loader-size: ($size * 3) + ($margin * 2);

$background-color:  #1fbeca;


body{
    font-family: 'Roboto',sans-serif;
    font-weight: 300;
    background: #D9D9D9;
    position:relative;
    width: 100%;
    max-width:100%;
    overflow-x: hidden;
    #app{
        padding: 100px 0;
        width: 100%;
        overflow-x: hidden;
    }
    .fixed_header{
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        background: #083C5D;
        z-index:3;
        color: #fff;
        line-height: 35px;
    }
}



h1.greet-message{
  text-align: center;
  font-weight: 300;
}
ul.list-group{
  list-style: none;
  margin:20px auto;
  padding:0;
  text-align: center;
  li{
    display: inline-block;
    padding: 10px;
    background: #FFEB3B;
    margin: 0 5px;
    transform: skewX(7deg) skewY(2deg);
  }
}
.fork-me{
  position: fixed;
  top: 0;
  right: 0;
  border: 0;
  max-width: 50%;
  //width:100px;
  height:90px;
  z-index:4;
  display:none;
  cursor:pointer;
  img{
    max-width: 100%;
    max-height: 100%;
    cursor:pointer;
  }

}

.pixel_color_input{
    border: 5px solid #000;
}
.pixel_tools_menu{
    position:relative;
    display: block;
    margin:0;
    padding:0;
    .pixel_tool{
        padding: 10px;
        box-sizing: border-box;
        display: inline-block;
        height: 60px;
        vertical-align: middle;
         .page-link.icon_item{
             padding-top:0; padding-bottom:0;
             padding-left: .5rem; padding-right: .5rem;
             svg{
                max-height: 36px;
                margin: 0;
                display: block;
             }
         }
        .color_swatch{
            width: 60px;
            height: 100%;
            position: relative;
            display: block;
            box-shadow: inset 0 0 0px 5px #fff;
            svg{
                width: 20px;
                padding: 1px;
                display: block;
                margin: 0 auto;
            }
            .twitter-picker{

                display: none!important;
                width: 280px!important;
                position: absolute!important;
                bottom: -55px!important;
                z-index: 13!important;
                &.open{
                    display: flex!important;
                    z-index: 13!important;
                }
            }
        }
        .btn{
            display: block;
            height: 100%;
            padding:0 1rem;
            svg{
                height: 100%;
            }
        }
        svg{
            width: 20px;
            height: 40px;
            fill: #fff;
        }
    }
}
.css-preview-block{
    position: relative;
}
textarea{
    width: 100%;
    min-height: 200px;
}

.canvas_wrap{
    position: relative;
    canvas.grid_canvas{
        position:absolute;
        top:0;
        left:0;
        z-index:0;
    }
    canvas.pixel_canvas{
        position:relative;
        top:0;
        left:0;
        z-index:2;
    }
}
.container{
    position:inherit;
}
.css-code-block{
    position: absolute;
    top: 150px;
    right: -400px;
    width: 550px;
    max-width:100%;
    height: 250px;
    transition: right 1s;
    &:hover{
        right: 0px;
    }
    textarea{width:100%; height:100%;}
}

@media only screen and (min-width: 600px){
    .fork-me{
        display:block;
    }
}





